.top-panel {
  width: 400px;
}

.score-div {
  text-align: center;
  padding-top: 5px;
  width: 80px;
  height: 70px;
  padding-bottom: 0px;
  border-radius: 0.3rem;
  background-color: #bbada0;
}

#score-label {
  font-weight: bold;
  color: #eee4da;
  margin: 0px;
}

#score-counter {
  color: white;
  font-size: 25px;
  font-weight: bold;
}

#logo {
  float: left;
  font-family: "Clear Sans";
  font-size: 80px;
  font-weight: bold;
  margin: 0px;
  color: #776e65;
}

.phrase {
  font-weight: 100;
  font-size: 18px;
}

#btn-new-game {
  font-weight: bold;
  width: 150px;
  margin: 50px;
  margin-right: 0px;
  color: white;
  background-color: #8f7a66;
}
