#main-container {
  display: flex;
  height: 100%;
  justify-content: center;
}

#game-grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  width: 400px;
  height: 400px;
  background: #bbada0;
  padding: 0.25rem;
  border-radius: 0.5rem;
}

.game-cell {
  font-family: "Clear Sans";
  display: flex;
  font-weight: bold;
  background: rgba(238, 228, 218, 0.35);
  border-radius: 0.3rem;
  margin: 0.25rem;
  font-size: 55px;
  justify-content: center;
  align-items: center;
  color: #776e65;
}

.cell-1 {
  background: #eee4da;
}

.cell-2 {
  background: #eee1c9;
}

.cell-3 {
  background: #f3b27a;
  color: #f9f6f2;
}

.cell-4 {
  background: #f69664;
  color: #f9f6f2;
}

.cell-5 {
  background: #f77c5f;
  color: #f9f6f2;
}

.cell-6 {
  background: #f75f3b;
  color: #f9f6f2;
}

.cell-7 {
  background: #edd073;
  font-size: 45px;
  color: #f9f6f2;
}

.cell-8 {
  background: #edcc62;
  box-shadow: 0 0 30px 10px rgba(243, 215, 116, 0.3174603175),
    inset 0 0 0 1px rgba(255, 255, 255, 0.1904761905);
  font-size: 45px;
  color: #f9f6f2;
}
